.accordion {
  width: 100%;
}
.accordion--background {
  background-color: #f1f1f1;
}
.accordion__headline {
  margin-bottom: 40px;
}
.accordion__content {
  border-bottom: 1px solid #d7d7d7;
  display: block;
  margin: 0 auto;
  padding: 0;
  width: 100%;
}
@media (min-width: 576px) {
  .accordion__content {
    width: 520px;
  }
}
@media (min-width: 768px) {
  .accordion__content {
    width: 700px;
  }
}
@media (min-width: 992px) {
  .accordion__content {
    width: 940px;
  }
}
.accordion__content--narrowed {
  max-width: 350px;
}
.accordion__item {
  border-top: 1px solid #d7d7d7;
  margin: 0;
  padding: 15px 0;
}
.accordion__trigger {
  background: none;
  border: 0;
  color: black;
  display: block;
  font-size: 1rem;
  font-weight: normal;
  margin: 0;
  outline: none;
  padding: 0;
  position: relative;
  text-align: left;
  width: 100%;
}
.accordion__trigger[aria-expanded=true] .accordion__label {
  letter-spacing: 0.05em;
  text-shadow: 1px 0 0 #000;
}
.accordion__trigger[aria-expanded=true] .accordion__label::after {
  transform: translateY(-50%) rotate(-135deg);
}
.accordion__trigger[aria-expanded=true] .accordion__icon::before, .accordion__trigger[aria-expanded=true] .accordion__icon::after {
  height: 2px;
}
.accordion__trigger[aria-expanded=true] .accordion__icon::before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.accordion__trigger[aria-expanded=true] .accordion__icon::after {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.accordion__label {
  display: block;
  font-family: "Helvetica-Neue-LTW-Light", "Segoe UI", "Open Sans", "Helvetica Neue", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.3;
  min-height: 53px;
  padding: 15px 40px 15px 15px;
  pointer-events: none;
  transition: all 0.1s ease-out;
}
@media screen and (prefers-reduced-motion: reduce) {
  .accordion__label {
    transition: none;
  }
}
.accordion__icon {
  display: inline-block;
  height: 1.25rem;
  pointer-events: none;
  position: absolute;
  right: 15px;
  top: 15px;
  width: 1.25rem;
}
.accordion__icon::before, .accordion__icon::after {
  background-color: black;
  content: "";
  height: 1px;
  position: absolute;
  top: 0.5rem;
  transition: all 0.15s ease-out;
  width: 0.75rem;
}
@media screen and (prefers-reduced-motion: reduce) {
  .accordion__icon::before, .accordion__icon::after {
    transition: none;
  }
}
.accordion__icon::before {
  left: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
@media screen and (prefers-reduced-motion: reduce) {
  .accordion__icon::before {
    transition: none;
  }
}
.accordion__icon::after {
  right: 0;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
@media screen and (prefers-reduced-motion: reduce) {
  .accordion__icon::after {
    transition: none;
  }
}
.accordion__panel {
  margin: 0;
  overflow: hidden;
  padding: 0 15px;
  transition: max-height 0.12s ease-out;
  will-change: max-height;
  /* For Edge bug https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/4806035/ */
}
@media screen and (prefers-reduced-motion: reduce) {
  .accordion__panel {
    transition: none;
  }
}
.accordion__panel > *:last-child {
  padding-bottom: 30px;
}
.accordion__panel[hidden] {
  display: none;
}
.accordion--narrow .accordion__content {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 576px) {
  .accordion--narrow .accordion__content {
    width: 420px;
  }
}
@media (min-width: 768px) {
  .accordion--narrow .accordion__content {
    width: 570px;
  }
}
@media (min-width: 992px) {
  .accordion--narrow .accordion__content {
    width: 610px;
  }
}
@media (min-width: 1200px) {
  .accordion--narrow .accordion__content {
    width: 730px;
  }
}
@media (min-width: 1600px) {
  .accordion--narrow .accordion__content {
    width: 948px;
  }
}

#site-wrapper:not(.page) .accordion--narrow .accordion__content {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 576px) {
  #site-wrapper:not(.page) .accordion--narrow .accordion__content {
    width: 100%;
  }
}
@media (min-width: 768px) {
  #site-wrapper:not(.page) .accordion--narrow .accordion__content {
    width: 700px;
  }
}
@media (min-width: 992px) {
  #site-wrapper:not(.page) .accordion--narrow .accordion__content {
    width: 640px;
  }
}
@media (min-width: 1200px) {
  #site-wrapper:not(.page) .accordion--narrow .accordion__content {
    width: 730px;
  }
}